import React, { useEffect, useState } from 'react';
import Link from 'next/link';
import {Button, Row, Avatar} from 'antd';

const dummy_user = {
    nickname:'user1',
    prefer_language:['react-native', 'java', 'c/c#'],
}

const MypageLeft = ({ children }) => {
    return (
        <>
            <Row style={{marginTop:'50px'}}>{<Avatar>{dummy_user.nickname[0]}</Avatar>}</Row>
            <Row>{dummy_user.nickname}</Row>
            <Row style={{margin:'40px 20px 40px 20px'}}>
                <Button href='myFavoriteLectures' style={{padding:'10px', width:'100%',backgroundColor:'#464646', color:'white', fontSize:'0.8em'}}>
                    <div >
                        즐겨찾기한 강의
                    </div>
                </Button>
                <Link href='myFavoriteSites'>
                    <div style={{padding:'10px', width:'100%',backgroundColor:'#464646', color:'white', fontSize:'0.8em'}}>
                        즐겨찾기한 사이트
                    </div>
                </Link>
                <Link href='myreview'>
                    <div style={{padding:'10px', width:'100%',backgroundColor:'#464646', color:'white', fontSize:'0.8em'}}>
                        내가 쓴 리뷰
                    </div>
                </Link>
                <Link href='mypost'>
                    <div style={{padding:'10px', width:'100%',backgroundColor:'#464646', color:'white', fontSize:'0.8em'}}>
                        내가 쓴 글
                    </div>
                </Link>
                <Link href='mycomment'>
                    <div style={{padding:'10px', width:'100%',backgroundColor:'#464646', color:'white', fontSize:'0.8em'}}>
                        내가 쓴 댓글
                    </div>
                </Link>
            </Row>
        </>
    );
  };
  export default MypageLeft;
  